<template>
    <div class="content">
        <div class="author">
            <div class="author name" v-for="item in musicDetailsName" :key="item.id">
                {{item.name}}
            </div>
        </div>
        <div class="banner-img">
            <img :src="musicDetailsImg" alt="" style="width:550rpx;height:550rpx" :class="{'xuanzhuan': xuanzhuan}">
        </div>

        <swiper class="musicComment" :autoplay='true' :interval='3000' :circular='true' :vertical='true' :display-multiple-items='3'>
            <block v-for="item in musicComment" :key="item.id">
                <swiper-item class="musicCommentItem">
                    <img :src="item.user.avatarUrl" alt=""> ：{{item.content}}
                </swiper-item>
            </block>
        </swiper>

        <div class="musicMini">
            <i :style="dianzan.zhuangtai?dianzan.zhen:dianzan.jia" @click="dianzanClick"></i>
            <i style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
            <i style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
            <i style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
        </div>

        <div class="musicIcon">
            <i style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
            <i @click="PrevSong" style="background:url(data:image/svg+xml;base64,); background-size:76rpx 76rpx; width:76rpx;height:76rpx;"></i>
            <i @click="listenerButtonPause" v-bind:style="[zanting.zhuangtai ? zanting.zanting : zanting.kaishi, errorClass]"></i>
            <i @click="nextSong" style="background:url(data:image/svg+xml;base64,); background-size:76rpx 76rpx; width:76rpx;height:76rpx;"></i>
            <i @click="playing" style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
        </div>

    </div>
</template>

<script>
var Fly = require("flyio/dist/npm/wx"); //npm引入方式
var fly = new Fly(); //创建fly实例
export default {
    data() {
        return {
            id: "",
            index: 0,
            musicUrlData: "",
            musicDetailsData: {},
            musicComment: {},
            musicDetailsImg: "",
            musicDetailsName: "",
            status: true,
            node: true,
            xuanzhuan: true,
            zanting: {
                zhuangtai: true,
                zanting:
                    "background:url(data:image/svg+xml;base64,);background-size:120rpx 120rpx; width:120rpx;height:120rpx;",
                kaishi:
                    "background:url(data:image/svg+xml;base64,);background-size:120rpx 120rpx; width:120rpx;height:120rpx;"
            },
            dianzan: {
                zhuangtai: false,
                zhen:
                    "background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;",
                jia:
                    "background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"
            }
        };
    },
    onLoad: function(options) {
        this.status = true;
        wx.setStorageSync("status", true);
        this.xuanzhuan = true;
        this.zanting.zhuangtai = true;
        this.id = options.id;

        let playing = wx.getStorageSync("playing");
        let chongfu = [];
        for (let index = 0; index < playing.length; index++) {
            const element = playing[index];

            if (element.id == this.id) {
                element.status = true;

                var elementIDxiangtong = true;
            } else {
                element.status = false;
            }
            chongfu.push(element.id);
        }

        for (let index = 0; index < chongfu.length; index++) {
            let elementq = chongfu[index];
        }

        if (!elementIDxiangtong) {
            let status = {
                id: this.id,
                status: true
            };
            playing.unshift(status);
        }

        wx.setStorageSync("playing", playing);

        var musicUrl = "http://localhost:3000/music/url?id=" + this.id;
        var musicDetails = "http://localhost:3000/song/detail?ids=" + this.id;
        var musicComment =
            "http://localhost:3000/comment/music?id=" + this.id + "&limit=100";

        const backgroundAudioManager = wx.getBackgroundAudioManager();

        let musicId = wx.getStorageSync("musicId");

        // 获取是否收藏缓存，首次因为还未设置所以为 false, (因为缓存为键值对格式，所以初始化时候要用{}把它包起来，直接初始化成数组)
        var postsCollected = wx.getStorageSync("posts_collected");
        // 打开一个新的页面，因为前一个页面有了缓存集，所以会进行这一步
        if (postsCollected) {
            var postcollected = postsCollected[this.id];

            // 如果是一个新的页面，那么给collected传递一个undefind会报错
            this.dianzan.zhuangtai = postcollected;
        } else {
            //首次直接跳到else
            var _postcollected = {};
            _postcollected = false;
            // 把初始化的值变为数组 ！
            wx.setStorageSync("posts_collected", {
                postcollected: _postcollected
            });
        }

        if (this.id == musicId) {
            fly
                .get(musicUrl, {})
                .then(d => {
                    this.musicUrlData = d.data.data[0].url;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });
        } else {
            wx.setStorageSync("musicId", this.id);

            fly
                .get(musicUrl, {})
                .then(d => {
                    this.musicUrlData = d.data.data[0].url;

                    backgroundAudioManager.src = this.musicUrlData;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });
        }

        fly
            .get(musicDetails, {})
            .then(d => {
                this.musicDetailsData = d.data;
                this.musicDetailsImg = d.data.songs[0].al.picUrl;
                let musicDetailsTitle = d.data.songs[0].al.name;
                this.musicDetailsName = d.data.songs[0].ar;

                backgroundAudioManager.title = musicDetailsTitle;
                backgroundAudioManager.singer = this.musicDetailsName;
                backgroundAudioManager.coverImgUrl = this.musicDetailsImg;

                wx.setNavigationBarTitle({
                    title: musicDetailsTitle //页面标题为路由参数
                });
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
        fly
            .get(musicComment, {})
            .then(d => {
                this.musicComment = d.data.comments;
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
    },

    mounted() {
        /**
         * 监听音乐播放
         */

        wx.onBackgroundAudioPlay(function() {
            // callback
            console.log("onBackgroundAudioPlay");
        });
        /**
         * 监听音乐暂停
         */

        wx.onBackgroundAudioPause(function() {
            // callback
            console.log("onBackgroundAudioPause");
        });

        wx.getBackgroundAudioManager().onStop(()=>{
            wx.setStorageSync("playing",[]);
            wx.setStorageSync("status", false);
            wx.setStorageSync("musicId", '');

            const url = "../index/main";
            wx.navigateTo({ url });
        })

        wx.getBackgroundAudioManager().onEnded(()=>{
            let playListziran = wx.getStorageSync("playing");
            for (let index = 0; index < playListziran.length; index++) {
                const element = playListziran[index].id;
                if (this.id == element) {
                    this.index = index;
                    this.id = playListziran[index - 1].id;
                    break;
                }
            }
            var musicUrl = "http://localhost:3000/music/url?id=" + this.id;
            var musicDetails =
                "http://localhost:3000/song/detail?ids=" + this.id;
            var musicComment =
                "http://localhost:3000/comment/music?id=" +
                this.id +
                "&limit=100";
            const backgroundAudioManager = wx.getBackgroundAudioManager();

            this.status = true;

            // 获取是否收藏缓存，首次因为还未设置所以为 false, (因为缓存为键值对格式，所以初始化时候要用{}把它包起来，直接初始化成数组)
            var postsCollected = wx.getStorageSync("posts_collected");
            // 打开一个新的页面，因为前一个页面有了缓存集，所以会进行这一步
            if (postsCollected) {
                var postcollected = postsCollected[this.id];

                // 如果是一个新的页面，那么给collected传递一个undefind会报错
                this.dianzan.zhuangtai = postcollected;
            } else {
                //首次直接跳到else
                var _postcollected = {};
                _postcollected = false;
                // 把初始化的值变为数组 ！
                wx.setStorageSync("posts_collected", {
                    postcollected: _postcollected
                });
            }

            wx.setStorageSync("musicId", this.id);
            fly
                .get(musicUrl, {})
                .then(d => {
                    this.musicUrlData = d.data.data[0].url;

                    backgroundAudioManager.src = this.musicUrlData;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });

            fly
                .get(musicDetails, {})
                .then(d => {
                    this.musicDetailsData = d.data;
                    this.musicDetailsImg = d.data.songs[0].al.picUrl;
                    let musicDetailsTitle = d.data.songs[0].al.name;
                    this.musicDetailsName = d.data.songs[0].ar;

                    backgroundAudioManager.title = musicDetailsTitle;
                    backgroundAudioManager.singer = this.musicDetailsName;
                    backgroundAudioManager.coverImgUrl = this.musicDetailsImg;

                    wx.setNavigationBarTitle({
                        title: musicDetailsTitle //页面标题为路由参数
                    });
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });
            fly
                .get(musicComment, {})
                .then(d => {
                    this.musicComment = d.data.comments;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });

            let chongfus = [];

            for (let index = 0; index < playListziran.length; index++) {
                const element = playListziran[index];

                if (element.id == this.id) {
                    element.status = true;

                    var elementIDxiangtong = true;
                } else {
                    element.status = false;
                }
                chongfus.push(element.id);
            }

            for (let index = 0; index < chongfus.length; index++) {
                let elementq = chongfus[index];
            }

            if (!elementIDxiangtong) {
                let status = {
                    id: this.id,
                    status: true
                };
                playListziran.unshift(status);
            }

            wx.setStorageSync("playing", playListziran);
        })
    },
    methods: {
        //监听button暂停按钮
        listenerButtonPause: function() {
            if (this.status) {
                wx.pauseBackgroundAudio({});
                this.status = false;
                wx.setStorageSync("status", false);
            } else {
                wx.playBackgroundAudio({});
                this.status = true;
                wx.setStorageSync("status", true);
            }

            if (this.xuanzhuan) {
                this.xuanzhuan = false;
            } else {
                this.xuanzhuan = true;
            }
            if (this.zanting.zhuangtai) {
                this.zanting.zhuangtai = false;
            } else {
                this.zanting.zhuangtai = true;
            }
        },
        dianzanClick: function() {
            var postsCollected = wx.getStorageSync("posts_collected");
            var postcollected = this.id;

            postsCollected[postcollected] = !postsCollected[postcollected];

            // 更新文章是否收藏缓存集
            //重新写入缓存集
            wx.setStorageSync("posts_collected", postsCollected);

            this.dianzan.zhuangtai = !this.dianzan.zhuangtai;
        },
        playing: function() {
            wx.switchTab({
                url: "../playlist/main" //注意switchTab只能跳转到带有tab的页面，不能跳转到不带tab的页面
            });
        },
        PrevSong: function() {
            
            let playListPrev = wx.getStorageSync("playing");
            for (let index = 0; index < playListPrev.length; index++) {
                const element = playListPrev[index].id;
                if (this.id == element) {
                    this.index = index;
                    this.id = playListPrev[index + 1].id;
                    break;
                }
            }
            var musicUrl = "http://localhost:3000/music/url?id=" + this.id;
            var musicDetails =
                "http://localhost:3000/song/detail?ids=" + this.id;
            var musicComment =
                "http://localhost:3000/comment/music?id=" +
                this.id +
                "&limit=100";
            const backgroundAudioManager = wx.getBackgroundAudioManager();

            this.status = true;

            // 获取是否收藏缓存，首次因为还未设置所以为 false, (因为缓存为键值对格式，所以初始化时候要用{}把它包起来，直接初始化成数组)
            var postsCollected = wx.getStorageSync("posts_collected");
            // 打开一个新的页面，因为前一个页面有了缓存集，所以会进行这一步
            if (postsCollected) {
                var postcollected = postsCollected[this.id];

                // 如果是一个新的页面，那么给collected传递一个undefind会报错
                this.dianzan.zhuangtai = postcollected;
            } else {
                //首次直接跳到else
                var _postcollected = {};
                _postcollected = false;
                // 把初始化的值变为数组 ！
                wx.setStorageSync("posts_collected", {
                    postcollected: _postcollected
                });
            }

            wx.setStorageSync("musicId", this.id);
            fly
                .get(musicUrl, {})
                .then(d => {
                    this.musicUrlData = d.data.data[0].url;

                    backgroundAudioManager.src = this.musicUrlData;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });

            fly
                .get(musicDetails, {})
                .then(d => {
                    this.musicDetailsData = d.data;
                    this.musicDetailsImg = d.data.songs[0].al.picUrl;
                    let musicDetailsTitle = d.data.songs[0].al.name;
                    this.musicDetailsName = d.data.songs[0].ar;

                    backgroundAudioManager.title = musicDetailsTitle;
                    backgroundAudioManager.singer = this.musicDetailsName;
                    backgroundAudioManager.coverImgUrl = this.musicDetailsImg;

                    wx.setNavigationBarTitle({
                        title: musicDetailsTitle //页面标题为路由参数
                    });
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });
            fly
                .get(musicComment, {})
                .then(d => {
                    this.musicComment = d.data.comments;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });

            let chongfuss = [];

            for (let index = 0; index < playListPrev.length; index++) {
                const element = playListPrev[index];

                if (element.id == this.id) {
                    element.status = true;

                    var elementIDxiangtong = true;
                } else {
                    element.status = false;
                }
                chongfuss.push(element.id);
            }

            for (let index = 0; index < chongfuss.length; index++) {
                let elementq = chongfuss[index];
            }

            if (!elementIDxiangtong) {
                let status = {
                    id: this.id,
                    status: true
                };
                playListPrev.unshift(status);
            }

            wx.setStorageSync("playing", playListPrev);
        },
        nextSong: function() {
            let playList = wx.getStorageSync("playing");
            for (let index = 0; index < playList.length; index++) {
                const element = playList[index].id;
                if (this.id == element) {
                    this.index = index;
                    this.id = playList[index - 1].id;
                    break;
                }
            }
            var musicUrl = "http://localhost:3000/music/url?id=" + this.id;
            var musicDetails =
                "http://localhost:3000/song/detail?ids=" + this.id;
            var musicComment =
                "http://localhost:3000/comment/music?id=" +
                this.id +
                "&limit=100";
            const backgroundAudioManager = wx.getBackgroundAudioManager();

            this.status = true;

            // 获取是否收藏缓存，首次因为还未设置所以为 false, (因为缓存为键值对格式，所以初始化时候要用{}把它包起来，直接初始化成数组)
            var postsCollected = wx.getStorageSync("posts_collected");
            // 打开一个新的页面，因为前一个页面有了缓存集，所以会进行这一步
            if (postsCollected) {
                var postcollected = postsCollected[this.id];

                // 如果是一个新的页面，那么给collected传递一个undefind会报错
                this.dianzan.zhuangtai = postcollected;
            } else {
                //首次直接跳到else
                var _postcollected = {};
                _postcollected = false;
                // 把初始化的值变为数组 ！
                wx.setStorageSync("posts_collected", {
                    postcollected: _postcollected
                });
            }

            wx.setStorageSync("musicId", this.id);
            fly
                .get(musicUrl, {})
                .then(d => {
                    this.musicUrlData = d.data.data[0].url;

                    backgroundAudioManager.src = this.musicUrlData;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });

            fly
                .get(musicDetails, {})
                .then(d => {
                    this.musicDetailsData = d.data;
                    this.musicDetailsImg = d.data.songs[0].al.picUrl;
                    let musicDetailsTitle = d.data.songs[0].al.name;
                    this.musicDetailsName = d.data.songs[0].ar;

                    backgroundAudioManager.title = musicDetailsTitle;
                    backgroundAudioManager.singer = this.musicDetailsName;
                    backgroundAudioManager.coverImgUrl = this.musicDetailsImg;

                    wx.setNavigationBarTitle({
                        title: musicDetailsTitle //页面标题为路由参数
                    });
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });
            fly
                .get(musicComment, {})
                .then(d => {
                    this.musicComment = d.data.comments;
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });

            let chongfus = [];

            for (let index = 0; index < playList.length; index++) {
                const element = playList[index];

                if (element.id == this.id) {
                    element.status = true;

                    var elementIDxiangtong = true;
                } else {
                    element.status = false;
                }
                chongfus.push(element.id);
            }

            for (let index = 0; index < chongfus.length; index++) {
                let elementq = chongfus[index];
            }

            if (!elementIDxiangtong) {
                let status = {
                    id: this.id,
                    status: true
                };
                playList.unshift(status);
            }

            wx.setStorageSync("playing", playList);
        }
    }
};
</script>

<style scoped>
.content {
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 0 40rpx;
}
.author {
    font-size: 24rpx;
    text-align: center;
    color: #999;
    margin-top: 6rpx;
    display: flex;
    justify-content: center;
    align-items: center;
}
.author div {
    margin-right: 10rpx;
}
.banner-img {
    width: 550rpx;
    height: 550rpx;
    border-radius: 50%;
    border: 10rpx solid #7054dd;
    margin: 46rpx auto 0;
    overflow: hidden;
}
.xuanzhuan {
    animation: rotate 60s linear infinite;
}
.musicMini {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    bottom: 200rpx;
    width: 670rpx;
}
.musicIcon {
    position: fixed;
    bottom: 30rpx;
    width: 670rpx;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.musicComment {
    margin-left: 40rpx;
    margin-top: 40rpx;
    width: 630rpx;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 220rpx;
    overflow: hidden;
}
.musicCommentItem {
    font-size: 24rpx;
    line-height: 60rpx;
    position: relative;
    padding-left: 70rpx;
    color: #666;
}
.musicComment .musicCommentItem img {
    width: 60rpx;
    height: 60rpx;
    border-radius: 50%;
    border: 1px solid #7054dd;
    position: absolute;
    left: 0;
    top: 0;
}
@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
</style>



